export default{
	props:{
		shadeShow:{
			value:Boolean,
			default:true
		},
		shadeClick:{
			value:Boolean,
			default:false
		},
		custom:{
			value:Boolean,
			default:false
		},
		type:{
			value:Number,
			default:1
		},
		width:{
			value:String,
			default:"450rpx"
		},
		height:{
			value:String,
			default:"300rpx"
		},
		backgroundColor:{
			value:String,
			default:"#fff"
		},
		callback:{
			type:Function,
			default:function () {
				
			}
		}
	},
	data(){
		return{
			isPopup:false,
			ani:''
		}
	},
	methods:{
		open:function(){
			this.isPopup=true;
			this.$nextTick(() => {
				setTimeout(() => {
					this.ani = 'open-animation'
				}, 30);
			})
		},
		close:function(v){
			let isClose = v == false ? false:true;
			if(isClose){
				this.ani = ''
				setTimeout(() => {
					this.isPopup=false;
					this.$emit('callback');
				}, 200);
			}	
		},
	}
}
